import React from 'react';
import './sidebar.css';
import Logo from "../../assets/logo.svg";

const Sidebar = ({ isNightMode }) => {
  return (
    <aside className='aside'>
        <a href="#home" className="nav__logo">
            <img src={Logo} alt="Logo" />
        </a>

        <nav className="nav">
            <div className="nav__menu">
                <ul className="nav__list">
                    <li className="nav__item">
                        <a href="#Home" className={`nav__link ${isNightMode ? "night-mode" : "light-mode"}`}>
                            <i className="icon-home"></i>
                        </a>
                    </li>
                    <li className="nav__item">
                        <a href="#About" className={`nav__link ${isNightMode ? "night-mode" : "light-mode"}`}>
                            <i className="icon-user-following"></i>
                        </a>
                    </li>
                    <li className="nav__item">
                        <a href="#Services" className={`nav__link ${isNightMode ? "night-mode" : "light-mode"}`}>
                            <i className="icon-briefcase"></i>
                        </a>
                    </li>
                    <li className="nav__item">
                        <a href="#Resume" className={`nav__link ${isNightMode ? "night-mode" : "light-mode"}`}>
                            <i className="icon-graduation"></i>
                        </a>
                    </li>
                    <li className="nav__item">
                        <a href="#Portfolio" className={`nav__link ${isNightMode ? "night-mode" : "light-mode"}`}>
                            <i className="icon-layers"></i>
                        </a>
                    </li>
                    <li className="nav__item">
                        <a href="#blog" className={`nav__link ${isNightMode ? "night-mode" : "light-mode"}`}>
                            <i className="icon-note"></i>
                        </a>
                    </li>
                    <li className="nav__item">
                        <a href="#contact" className={`nav__link ${isNightMode ? "night-mode" : "light-mode"}`}>
                            <i className="icon-bubble"></i>
                        </a>
                    </li>
                </ul>
            </div>
        </nav>

        <div className="nav__footer">
            <span className="copyright">&copy; 2022-2023.</span>
        </div>
    </aside>
  );
};

export default Sidebar;
