import React, { useEffect } from 'react';
import './home.css';
import Me from "../../assets/pfp2.jpg";
import HeaderSocials from './HeaderSocials';
import ScrollDown from './ScrollDown';
import Typed from 'typed.js';

const Home = () => {
  useEffect(() => {
    const options = {
      strings: [
        "<span>Cloud</span><br/><span>Networking</span><br/><span>Software</span>"
      ],
      typeSpeed: 100, // Speed of typing
      backSpeed: 0, // Disable backspacing
      backDelay: 1000, // Delay before starting the next word
      startDelay: 500, // Delay before starting the typing
      loop: true, // Loop through the words infinitely
      showCursor: false, // Show the cursor
      smartBackspace: false, // Disable backspacing
      contentType: 'html', // Ensure the content type is HTML
    };

    const typed = new Typed(".home__education", options);

    return () => {
      typed.destroy();
    };
  }, []);

  return (
    <section className="home container" id="home">
      <div className="intro">
        <img src={Me} alt="Profile" className="home__img" />
        <h1 className="home__name">Deepak K</h1>
        <span className="home__education"></span>
        <HeaderSocials />
        <a href="#contact" className="btn">Contact</a>
        <ScrollDown />
      </div>
    </section>
  );
};

export default Home;
